import { DxHTMLElementBase } from "../common/dx-html-element-base";
import { GridCssClasses } from "./grid-css-classes";
import * as PointerEvents from "../common/pointer-events";
export const DxGridHeaderContentTagName = "dxbl-grid-header-content";
export class DxGridHeaderContent extends DxHTMLElementBase {
    constructor() {
        super();
        this.handlePointerEventsMode = PointerEvents.HandlePointerEventsMode.Click | PointerEvents.HandlePointerEventsMode.Dragging;
    }
    get handlePointerEventsTarget() {
        return this.closest(`.${GridCssClasses.HeaderElementClassName}`);
    }
    get handlePointerEventsDelay() {
        return PointerEvents.TapInteractionTimeout;
    }
}
