export class GridRequestStartCellEditingContext {
    constructor(cellInfo) {
        this.cellInfo = cellInfo;
    }
}
export class GridStartCellEditingRequestCompleteContext {
    constructor(result) {
        this.result = result;
    }
}
export class GridRequestStartCellEditingEvent extends CustomEvent {
    constructor(detail) {
        super(GridRequestStartCellEditingEvent.eventName, {
            detail,
            bubbles: true,
            composed: false,
            cancelable: false,
        });
    }
}
GridRequestStartCellEditingEvent.eventName = "dxbl-grid-request-start-editing";
export class GridStartCellEditingRequestCompleteEvent extends CustomEvent {
    constructor(result) {
        const detail = new GridStartCellEditingRequestCompleteContext(result);
        super(GridStartCellEditingRequestCompleteEvent.eventName, {
            detail,
            bubbles: true,
            composed: false,
            cancelable: false,
        });
    }
}
GridStartCellEditingRequestCompleteEvent.eventName = "dxbl-grid-cell-editor-shown";
